	module.exports = {"data":{"post":{"html":"<p>A lot of people are trying the <a href=\"http://www.okcupid.com/politics\">the politics test</a>. The German <a href=\"http://www.wahlomat.de/\">Wahl-O-Mat</a> called me a <a href=\"http://www.papascott.de/archives/2005/08/26/wahl-o-mat/\">Green Neo-Liberal</a>, this test calls me a Libertarian Democrat.</p>\n<blockquote><p>You are a Social Liberal (85% permissive) and an Economic Conservative (66% permissive) You are best described as a: <b>Libertarian</b>.  You exhibit a very well-developed sense of Right and Wrong and believe in economic fairness.</p></blockquote>\n<table id=\"thetable\" name=\"thetable\" background=\"http://is2.okcupid.com/graphics/politics/chart_political.gif\" border=\"0\" cellpadding=\"0\" cellspacing=\"0\" height=\"375\" width=\"375\">\n<tbody>\n<tr height=\"106\">\n<td width=\"299\"></td>\n<td width=\"75\"></td>\n</tr>\n<tr height=\"268\">\n<td width=\"299\"></td>\n<td align=\"left\" valign=\"top\" width=\"75\"><img src=\"http://is2.okcupid.com/graphics/politics_you.gif\" border=\"0\" /></td>\n</tr>\n</tbody>\n</table>\n<p> </p>\n<p>At least I'm consistent, even across borders.</p>","fields":{"title":"Libertarian Democrat","date":"25 Sep 2005","url":"https://papascott.de/2005-09-25-libertarian-democrat/"}}},"pathContext":{"slug":"/2005-09-25-libertarian-democrat/","prev":{"fields":{"title":"Rocket Science","date":"24 Sep 2005","slug":"/2005-09-24-rocket-science/"}},"next":{"fields":{"title":"Lie To Me","date":"25 Sep 2005","slug":"/2005-09-25-lie-to-me/"}}}}

